body {
  background-color: skyblue;
  text-align: center;
  width: 500px;
  display: flex;
  justify-content: center;
  margin: auto;
}

/* --------- */

.card {
  background-color: beige;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.1);
  cursor: pointer;
  transition: transform 0.3s ease-in-out;
}

.card:hover {
  transform: scale(1.05);
}

.detail-card {
  background-color: beige;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.1);
  margin: auto;
  max-width: 800px;
  padding: 20px;
}
